/**
 * ChatPanel.scss
 * Contains CSS rules for the chat panel that are not component specific.
 */
/**
 * This file contains variables and mixins that can be accessed by any
 * SASS module.
 */
#chat-panel {
  padding: 0;
  background-color: white;
  font-size: 14px;
  /* Needed to make sure #chat-messages works */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column;
      flex-flow: column;
  height: 100%;
  max-height: 100%; }

/**
 * Increase font-size on smaller viewports
 */
@media (max-width: 575px) {
  #chat-panel {
    font-size: 16px; } }

/**
 * The bar/section on top of the chat panel
 */
#chat-titlebar {
  background-color: #1edcf6;
  color: white;
  width: 100%;
  padding: 15px;
  border-bottom: 3px solid #09bfd8;
  /* h1 */ }
  #chat-titlebar #chat-name {
    margin: 0;
    font-size: 32px; }

#chat-messages {
  /* Make chat area take up all available vertical space */
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  overflow-y: scroll; }



