html, body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  width: 100%;
  height: 100%;
  background: url(../img/stardust.png);
  color: white; }

#fullscreen-app {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0; }
  #fullscreen-app #chat-wrapper {
    width: 100%;
    height: 100%; }

#app {
  padding-top: 30px; }
  #app > h1 {
    text-align: center; }
  #app #chat-wrapper {
    height: 600px;
    max-width: 400px;
    margin: 0 auto; }



