/**
 * ChatBubble.scss
 * This SASS module contains styles for the ChatBubble.js React Component.
 */
/**
 * This file contains variables and mixins that can be accessed by any
 * SASS module.
 */
/**
 * This mixin holds the CSS rules for chat bubbles regardless of if the
 * message is from me or from the other person.
 */
/**
 * These two classes are used depending on if the current message sender
 * is the same as the previous sender. If the senders are different, we want
 * to have some extra space above the message.
 */
.chat-consecutive {
  margin-top: 3px; }

.chat-new-sender {
  margin-top: 10px; }

/**
 * Chat bubbles for messages sent by me.
 */
.from-me {
  max-width: 75%;
  padding: 7px 15px;
  border-radius: 20px;
  clear: both;
  white-space: pre-line;
  overflow-wrap: break-word;
  background-color: #1edcf6;
  color: white;
  float: right;
  margin-left: auto; }

/**
 * Chat bubbles for messages sent from the other person.
 */
.from-them {
  max-width: 75%;
  padding: 7px 15px;
  border-radius: 20px;
  clear: both;
  white-space: pre-line;
  overflow-wrap: break-word;
  background-color: #f1f1f1;
  color: #202020;
  float: left;
  margin-right: auto; }

.chat-bubble-link {
  color: inherit;
  text-decoration: underline; }
  .chat-bubble-link:hover, .chat-bubble-link:active, .chat-bubble-link:focus {
    color: inherit; }



